    box-sizing: border-box;
}
img{
    max-width: 100%;
    height: auto;
}
@font-face {
    font-family: "Source Sans Pro";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
@font-face {
    font-family: "Source Sans Pro";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
