    font-style: italic;
}

body{
    font-family: "Source Sans Pro";
}

.cabecalho{
    background: #fff;
    padding: 15px;
}

.logo{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-repeat: no-repeat;
    display: block;
    width: 120px;
    height: 53px;
    text-indent: -9999px;
