    cursor: pointer;

    position: absolute;
    top: 15px;
    right: 15px;
}

.menu-principal__btn--fechar{
    background-position: -48px 7px;
}

.menu-principal__lista{
    padding: 0;
    list-style: none;
}

.menu-principal__item{
    text-decoration: none;
    font-weight: bold;
    color: #343434;
    margin-bottom: 10px;
    display: block;
    font-size: 18px;
}

.menu-principal__item--atual{
    color: #f44242;
}

.rodape{
    color: #fff;
}

.rodape .logo{
    margin-bottom: 20px;
}

.rodape__linha-1{
    background-color: #272525;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
    background-size: cover;
    padding: 15px;
}

.rodape__linha-2{
    background-color: #000;
    text-align: center;
    padding: 15px;
}

.rodape__coluna{
    margin-top: 40px;
    margin-bottom: 40px;
}

.rodape__titulo{
    border-bottom: 1px solid #fff;
    padding: 0 10px 10px 10px;
    font-size: 18px;
}

.rodape__lista-link{
    padding: 0;
    list-style: none;
}

.rodape__lista-link__item{
    color: #fff;
    text-decoration: none;
    margin-bottom: 10px;
    display: block;
}

.rodape__lista-link__item::before{
    content: '>';
    margin-right: 8px;
    color: #f44242;
}


/*Style paginas internas*/

.pagina-cabecalho{
    background-color: #dd3f3f;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
    background-attachment: fixed;
    color: #fff;
    padding-top: 40px;
    padding-bottom: 40px;
}

.pagina-cabecalho__titulo{
    text-align: center;
    text-transform: uppercase;
}

.pagina-cabecalho__titulo::after{
    content: '';
    display: block;
    width: 50px;
    height: 3px;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 20px;
}

.pagina-conteudo{
    padding: 15px;
    padding-top: 25px;
    padding-bottom: 25px;
}

.pagina-conteudo p{
    color: #3a3a3a;
    line-height: 22px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.text-center{
    text-align: center;
}

/*Style pagina portfolio*/

.lista-trabalhos{
    list-style: none;
    padding: 0;
}

.lista-trabalhos__item{
    margin-left: -15px;
    margin-right: -15px;
    margin-bottom: 50px;
}

.lista-trabalhos__item a{
    text-decoration: none;
}

.lista-trabalhos__titulo{
    color: #000;
    font-size: 18px;
    margin-left: 15px;
    border-left: 1px solid #000;
    padding-left: 10px;
}

/*Style pagina contato*/

.formulario{
    background-color: #efecec;
    margin-left: -15px;
    margin-right: -15px;
    padding: 40px 15px 40px 15px;
    box-shadow: 0 3px 5px #989898;
}

.formulario__campo{
    width: 100%;
    border: 1px solid #c4c4c4;
    border-radius: 5px;
    padding: 10px 5px;
}

.formulario__grupo{
    margin-bottom: 15px;
}

.formulario__label{
    color: #343434;
}

.formulario__botao{
    border: 3px solid #f00;
    border-radius: 300px;
    background-color: #fff;
    color: #f00;
    padding: 5px 20px;
    cursor: pointer;
    display: block;
    margin: 0 auto;
    font-weight: bold;
}

.mapa{
    height: 0;
    padding-bottom: 100%;
    position: relative;
    overflow: hidden;
    margin: 0 -15px -25px -15px;
}

.mapa iframe{
    width: 100% !important;
    height: 100% !important;
    position: absolute;
    top: 0;
    left: 0;
}

/*Style pagina home*/

.cabecalho-home{
    background: url(${___CSS_LOADER_URL_REPLACEMENT_8___}) #000 no-repeat;
    background-size: cover;
    color: #fff;
    text-align: center;
    padding: 50px 0;
}

.cabecalho-home__titulo{
    font-size: 40px;
    margin-bottom: 0;
}

.cabecalho-home__role{
    display: none;
}

.home__titulo{
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
}

.home__titulo::after{
    content: '';
    display: block;
    width: 50px;
    height: 3px;
    background-color: #f00;
    margin:  0 auto;
    margin-top: 15px;
}

.home__titulo--branco::after{
    background-color: #fff;
}

.servicos{
    padding: 30px 0;
}

.servicos__item{
    text-align: center;
    padding: 0 15px;
    margin-bottom: 50px;    
}

.servicos__texto{
    color: #585858;
}

