    margin: 0 auto;
    display: block;
    box-shadow: 3px 3px 0 #000;
    margin-bottom: 30px;
}

.depoimentos__texto{
    line-height: 24px;
    margin-bottom: 30px;
}

.depoimentos__texto::before{
    background: url(${___CSS_LOADER_URL_REPLACEMENT_9___}) no-repeat;
    content: '';
    display: inline-block;
    width: 25px;
    height: 23px;
    margin-right: 5px;
}

.depoimentos__pessoa{
    text-align: center;
    font-weight: bold;
    font-style: italic;
